@import 'components/variables';
@import "components/generales/accordion";
@import 'components/generales/banner-dudas.scss';

.section-1 {
  padding-top: 3rem;
  padding-bottom: 3rem;
  .line-material {
    width: 6.5rem;
    height: .6rem;
    background-color: $blue-tabla4;
    opacity: 1;
  }
  ul {
    padding-left: 1rem;
    li {
      color: $gray-siroco;
      list-style: none;
      span {
        font-weight: 800;
      }
    }
  }
  .enlace-condiciones {
    color: $gray-siroco;
    font-weight: 600;
    text-decoration: none;
    word-break: break-all;
    transition: all 0.3s;
    span {
      font-weight: 800;
    }
    &:hover {
      color: $blue-siroco;
    }
  }
}
.section-2 {
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: $gray-claro;
}
