.accordion-button {
  padding: .6rem 1.25rem;
  font-family: "Roboto";
  font-weight: 300;
  color: $gray-siroco;
}
.accordion-button::after {
  background-image: url('../images/icons/plus-accordion.png');
}
.accordion-button:not(.collapsed)::after {
  background-image: url('../images/icons/plus-accordion.png');
  transform: rotate(-135deg);
}
.accordion-button:focus {
  z-index: 3;
  border-color: none;
  outline: 0;
  box-shadow: none;
}
